
.header-jumbotron {
  padding: 50px 5% 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-weight: 600;
  height: 100vh;
  font-size: 1.25rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background: -webkit-gradient(
    linear, 
    left top, right top, 
    from(rgba(0, 117, 212, .4)), 
    color-stop(50%, rgba(138, 74, 228, .4)),
    to(rgba(230, 50, 83, .4)));
  background: linear-gradient(
    to right, 
    rgba(0, 117, 212, .4) 0%, 
    rgba(138, 74, 228, .4) 50%,
    rgba(230, 50, 83, .4) 100%);
}
.container-logo {
  min-width: 100%;
}
.logos {
  margin-bottom: 1rem;
}
.logo-separator {
  font-size: 4.25rem;
  opacity: .75;
  margin: 0 .5rem;
}
.logo-separator, .sep-text {
  font-weight: 100;
}
.gc-logo {
  height: 4rem;
}
.vistec-logo {
  height: 4rem;
  padding-top: .95rem;
}
.gc-logo, .vistec-logo {
  margin-top: -1.5rem;
  margin-bottom: 1rem;
}
.horizontal-logo-separator {
  display: none;
  width: 15rem;
  margin: 0 auto;
}
.horizontal-logo-separator hr {
  border-width: .15rem;
  border-color: #212529;
  opacity: .6;
}
.header-jumbotron h1 {
  font-size: 3rem;
}
@media (max-width: 992px) {
.logo-separator {
    display: none;
}
.horizontal-logo-separator {
    display: block;
}
.gc-logo, .vistec-logo {
    margin: 0;
}
.vistec-logo {
    margin-top: -.95rem;
}
.logos {
    margin-bottom: 3rem;
}
.jumbotron-text h3 {
    font-size: 1.4rem !important;
}
}
.bg-stripe:nth-of-type(odd) {
  background-color: #f0f0f0 !important;
}



